.subscription-text-field {
	border: 1px solid $color-ds25;
	border-radius: 4px;
	height: 44px;
	width: 266px;
	margin-top: 5px;

	&:hover {
		border: 1px solid $color-ds50;
	}

	&__input {
		border-radius: 4px;
		border: none;
		height: 42px;
		outline: none;
		text-indent: 15px;
		width: 216px;
	}

	&__input::-webkit-input-placeholder {
		color: $color-ds25;
	}

	&__button {
		position: absolute;
		background: url(../img/arrow_forward.svg) no-repeat 50% 50%;
		height: 44px;
		width: 53px;
		border: none;
	}
}